import React from 'react';
import styled from 'styled-components';
import {Element} from 'react-scroll';
export default () => (
  <Element name="about-us">
    <Container>
      <div style={{margin: '40px 0'}} className="row d-flex align-items-center">
        <div className="col-lg-6 offset-lg-3 col-md-8 offset-md-2">
          <div style={{padding: '0 10px'}}>
            <h2 style={{textAlign: 'center'}}>
              Fair games that pay{' '}
              <span style={{color: 'red'}}>Super Red Logo</span>
            </h2>
            <p style={{textAlign: 'center'}}>
              Provably fair bets backed by simple open-sourced contract <br />{' '}
              No sign-ups or deposits, just 1% edge and jackpot!
            </p>
          </div>
        </div>
      </div>
    </Container>
  </Element>
);

const Container = styled.div`
  border-bottom: 1px solid #ebebeb;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
`;
