import React from 'react';
import styled from 'styled-components';

export default ({address, win}) => (
  <Container>
    <div className="row">
      <div className="col-6 d-flex align-items-center">
        <img
          src="data:image/svg+xml;base64,"
          className="image"
        />
        <a href="#" className="link">
          {address}
        </a>
      </div>
      <div className="col-6 d-flex align-items-center">
        <div className="win">
          {win}
          <span>ETH</span>
        </div>
      </div>
    </div>
  </Container>
);

const Container = styled.div`
  margin-top: 7px;
  img {
    width: 35px;
    height: 35px;
  }
  .link {
    margin-left: 10px;
    color: red;
    text-decoration: none;
  }
  .win {
    font-weight: 700;
    font-size: 20px;
  }
  .win span {
    margin-left: 7px;
    font-size: 15px;
  }
`;
